/**
 * Lineup component that queries for data
 * with Gatsby's StaticQuery component
 *
 * See: https://www.gatsbyjs.org/docs/static-query/
 */

import React, { useState, useEffect } from "react"
import { setTimes } from '../setTimes'

import './lineup.css'
import moment from 'moment'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faClock, faMapMarkerAlt, faPlusCircle } from '@fortawesome/free-solid-svg-icons'


const formatSchedule = (times) => {
  let groupedItems = {}
  times.sort((a, b) => {
    return new Date(moment(a.startTime)) - new Date(moment(b.startTime))
  }).map(item => {
    let key = item.day
    item.formatedStartTime = moment(item.startTime).format('LT')
    item.formatedEndTime = moment(item.endTime).format('LT')
    let time = item.formatedStartTime
    if (groupedItems[key]) {
      groupedItems[key][time]
        ? groupedItems[key][time].push(item)
        : groupedItems[key][time] = [item]
    } else {
      groupedItems[key] = {
        [time]: [item]
      }
    }
    return
  })
  return groupedItems
}

const groupItems = (pageTitle, mySchedule) => {
  let groupedItems = {}
  let times = JSON.parse(JSON.stringify(setTimes))
  switch (pageTitle) {
    case 'My Schedule':
      groupedItems = formatSchedule(mySchedule)
      break
    case 'Lineup':
      times.sort((a, b) => {
        if (a.artist < b.artist) { return -1; }
        if (a.artist > b.artist) { return 1; }
        return 0;
      }).map(item => {
        let key = item.artist.charAt(0)
        item.formatedStartTime = moment(item.startTime).format('LT')
        item.formatedEndTime = moment(item.endTime).format('LT')
        groupedItems[key] = groupedItems[key]
          ? [...groupedItems[key], item]
          : [item]
        return
      })
      break
    case 'Schedule':
      groupedItems = formatSchedule(times)
      break;
    default:
      break
  }
  return groupedItems
}

const Lineup = ({ pageTitle }) => {

  let [mySchedule, setMySchedule] = useState([])
  let [localStorageChecked, setLocalStorageChecked] = useState(false)
  const groupedItems = groupItems(pageTitle, mySchedule)

  useEffect(() => {
    if (!localStorageChecked) {
      let _mySchedule = localStorage.hasOwnProperty('mySchedule')
        ? JSON.parse(localStorage.getItem('mySchedule'))
        : null
      setLocalStorageChecked(true)
      if(_mySchedule) {
        setMySchedule(_mySchedule)
      }
    }
  })

  const addSet = (set) => {
    return () => {
      setMySchedule([...mySchedule, set])
      localStorage.setItem('mySchedule', JSON.stringify([...mySchedule, set]))
    }
  }

  const scheduleContainsSet = (set) => {
    let containsSet = false
    mySchedule.map(item => {
      if (item.artist === set.artist) {
        containsSet = true
      }
    })
    return containsSet
  }

  return (
    <div>
      {Object.entries(groupedItems).length > 0 ? Object.keys(groupedItems).map(key => (
        <div key={key}>
          {pageTitle === 'Lineup' ? (
            <div>
              <div className='list-header'>
                <span className='alphabet'>{key.toUpperCase()}</span>
              </div>
              {groupedItems[key].map(item => (
                <div className='list-content' key={item.artist}>
                  <p className='artist'>{item.artist}</p>
                  <p className={`stage stage-${item.stage.toLowerCase()}`}><FontAwesomeIcon icon={faMapMarkerAlt} /> {item.stage}</p>
                  <p className='set-times'><FontAwesomeIcon icon={faClock} /> {item.formatedStartTime} - {item.formatedEndTime}</p>
                  {!scheduleContainsSet(item) ? <span onClick={addSet(item)} className='add-button'><FontAwesomeIcon icon={faPlusCircle} /></span> : null}
                </div>
              ))}
            </div>
          ) : null}

          {pageTitle !== 'Lineup' ? (
            <div>
              <div className='list-header'>
                <h2 className='day'>{key}</h2>
              </div>
              {Object.keys(groupedItems[key]).map(time => (
                <div key={time}>
                  <div className='list-header'>
                    <span className='time'>{time}</span>
                  </div>
                  {groupedItems[key][time].map((item, i) => (
                    <div key={i} className='list-content'>
                      <p className='artist'>{item.artist}</p>
                      <p className={`stage stage-${item.stage.toLowerCase()}`}><FontAwesomeIcon icon={faMapMarkerAlt} /> {item.stage}</p>
                      <p className='set-times'><FontAwesomeIcon icon={faClock} /> {item.formatedStartTime} - {item.formatedEndTime}</p>
                      {pageTitle !== 'My Schedule' && !scheduleContainsSet(item) ? <span onClick={addSet(item)} className='add-button'><FontAwesomeIcon icon={faPlusCircle} /></span> : null}
                    </div>
                  ))}
                </div>
              ))}
            </div>
          ) : null}

        </div>
      )) : <div className='empty-schedule'>
          <p>Looks like you haven't planned your weekend yet. Checkout the <span>Lineup</span> and <span>Schedule</span> pages and hit the <span><FontAwesomeIcon icon={faPlusCircle} /></span> button to save items to your schedule.</p>
        </div>
      }
    </div>
  )
}

export default Lineup
