import { Link } from "gatsby"
import React from "react"

import Nav from 'react-bootstrap/Nav';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faClock, faPlusCircle, faList } from '@fortawesome/free-solid-svg-icons'
import './footer-tabs.css'

const FooterTabs = ({ activePage }) => (
  <Nav
    style={{
      margin: '0 auto',
      borderTop: '1px solid #ECECEC',
      height: 59
    }}
    activeKey="1">
    <Nav.Item className={`tab${activePage === 'My Schedule' ? ' active' : ''}`}>
      <Link to="/">
        <div>
          <FontAwesomeIcon icon={faPlusCircle} />
        </div>
        My Schedule
      </Link>
    </Nav.Item>
    <Nav.Item className={`tab${activePage === 'Lineup' ? ' active' : ''}`}>
      <Link to="/lineup">
        <div><FontAwesomeIcon icon={faList} /></div>
        Lineup
      </Link>
    </Nav.Item>
    <Nav.Item className={`tab${activePage === 'Schedule' ? ' active' : ''}`}>
      <Link to="/schedule">
        <div><FontAwesomeIcon icon={faClock} /></div>
        Schedule
      </Link>
    </Nav.Item>
  </Nav>
)

export default FooterTabs
